/* src/styles/Footer.css */

html, body {
  height: 100%;
  
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
}

body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

/* The main content should fill the remaining height, keeping the footer at the bottom */
main {
  flex: 1 0 auto;
}


.footer {
  background-color: #000;
  color: #fff;
  padding: 20px 0;
  width: 100%;
  text-align: left;
  position: relative;
  bottom: 0;
  flex-shrink: 0;
}

.footer .container {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
}

.footer h5 {
  font-family: 'Arial', 'sans-serif';
  font-weight: 500;
  color: #6c63ff;
  margin-bottom: 10px;
  font-size: 21px;
}

.footer__logo-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  margin-top: 50px;
}

.footer__logo-section img {
  width: 250px;
  border-radius: 5px;
}

.social-icons {
  display: flex;
  gap: 2px;
  margin-top: 10px;
  cursor: pointer;
}

.social-icons img {
  width: 35px;
  height: 35px;
  transition: filter 0.3s;
  
}

/* Base style for social icons */
.social-icons a {
  margin: 0 10px;
  display: inline-block;
  transition: transform 0.3s ease-in-out, filter 0.3s ease-in-out;
}

/* Facebook hover effect */
.facebook-icon:hover {
  transform: scale(1.2);
  filter: brightness(1.5);
}

/* Instagram hover effect */
.instagram-icon:hover img {
  transform: scale(1.2);
  filter: hue-rotate(45deg); /* Changes hue slightly */
}

/* Twitter hover effect */
.twitter-icon:hover img {
  transform: scale(1.2);
  filter: saturate(2); /* Increases saturation */
}

/* LinkedIn hover effect */
.linkedin-icon:hover img {
  transform: scale(1.2);
  filter: contrast(1.5); /* Increases contrast */
}


.footer__links {
  display: flex;
  gap: 80px;
  justify-content: flex-end;
  flex: 1;
  text-align: left;
}

.footer__links div {
  margin-top: 10px;
  font-size: 0.9rem;
  font-weight: 500;
}

.footer__links ul {
  list-style: none;
  padding: 0;
}

.footer__links ul li {
  margin-bottom: 5px;
}

.footer__links ul li a {
  text-decoration: none;
  color: #fff;
  font-size: 16px;
}

.footer__links ul li a:hover {
  text-decoration: none;
  color: #0061c8;
}

.footer__copyright {
  text-align: center;
  font-size: 0.9rem;
  color: #666;
  width: 100%;
  margin-top: 20px; 
}

/* Media Queries for Small Screens */
@media (max-width: 768px) {
  .footer .container {
    flex-direction: column;
    align-items: center;
    text-align: center;
  }

  .footer__logo-section {
    align-items: center;
    margin-bottom: 20px;
  }

  .footer__links {
    flex-direction: row;
    align-items: center;
    gap: 20px; /* Less gap for smaller screens */
    text-align: center;
  }

  .footer__links div {
    margin: 0; /* No extra margin in smaller screens */
  }

  .social-icons {
    justify-content: center;
    margin-top: 10px;
  }

  .footer__copyright {
    margin-top: 20px;
  }

  .terms-text h1 {
    font-size: 20px;
    margin-bottom: 10px;
    text-align: center;
  }
}

@media (min-width: 1024px) { 
  .footer .container {
    flex-direction: row;
    align-items: center;
    text-align: center;
    max-width: 90%;
    margin: 0 auto;
  }
}
